* {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    gap: 12rem;
    background-color: #FBF8EF;
}

@media (max-width: 768px) {
    body {
        gap: 6rem;
    }

    .no-scroll {
        overflow: hidden;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 1009;
    }
}

/* header - nav */

.nav-list > li:nth-of-type(1) > .navButton {
    font-weight: var(--font-weight-bold);
}

/* main */

.mainModels {
    display: flex;
    flex-direction: column;
    gap: 12.5rem;
}

@media (max-width: 768px) {
    .mainModels {
        gap: 8rem;
    }
}

/* article */

:root {
    --font-family: "Unbounded";
    --font-weight-bold: 800;
    --font-weight-regular: 400;
    --font-color: #292929;
}

.articleModels {
    position: relative;
    padding: 0rem 8rem;
    display: flex;
    flex-direction: column;
    gap: 15rem;
}

@media (max-width: 768px) {
    .articleModels {
        padding: 0rem 1.5rem;
    }
}

/* containerCar */


.typeContainer {
    display: flex;
    justify-content: space-between;
    position: relative;
    width: auto;
    height: auto;
}

@media (max-width: 768px) {
    .typeContainer {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem 1rem;
        width: 100%;
    }
}

.typeContainer h1 {
    position: absolute;
    top: -10%;
    font-family: var(--font-family);
    font-size: 2rem;
    font-weight: 600;
    color: #4F4F4F;
}

@media (max-width: 768px) {
    .typeContainer h1 {
        font-size: 1.2rem;
    }
}

.special .carLink:nth-child(4) .carContainer {
    visibility: hidden;
}

.carLink {
    text-decoration: none;
    max-width: 30%;
    height: auto;
}

.carContainer {
    padding: 3rem 1.5rem 2rem 1.5rem;
    background-color: #D9D9D9;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 35rem;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
    transition: all 0.3s ease;
}

@media (max-width: 768px) {
    .carContainer {
        padding: 2rem 0.5rem 1.5rem;
        width: 10rem;
        height: 20rem;
        box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    }
}

.carContainer:hover {
    transform: scale(1.03);
    background-color: #F1F1F1;
}

.carContainer h2, h3 {
    font-family: var(--font-family);
    font-size: 1.5rem;
    font-weight: var(--font-weight-regular);
    color: black;
}

.carContainer h3 {
    font-weight: 500;
}

@media (max-width: 768px) {
    .carContainer h2,
    .carContainer h3 {
        font-size: 1rem;
    }
}

.carContainer p {
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: 200;
    color: black;
}

@media (max-width: 768px) {
    .carContainer p {
        font-size: 0.5rem;
    }
}

.addInfo {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 2rem;
}

@media (max-width: 768px) {
    .addInfo {
        margin-bottom: 1rem;
        gap: 0.5rem 1rem;
    }
}

/* blockquote */

.text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    transition: all 0.3s ease;
}

@media (max-width: 768px) {
    .text {
        padding: 0rem 2rem;
    }
}

.text.visible {
    transform: translateY(0rem);
    opacity: 1;
}

.text p:first-of-type {
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: 600;
    color: var(--font-color);
}

.text p:nth-of-type(2) {
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: var(--font-weight-regular);
    color: var(--font-color);
    text-align: center;
    max-width: 600px;
}

@media (max-width: 768px) {
    .text p:first-of-type {
        font-size: 0.8rem;
    }

    .text p:last-of-type {
        font-size: 0.5rem;
    }
}

.otherModelsMessage {
    font-family: var(--font-family);
    display: flex;
    justify-content: center;
    text-align: center;
}

/* preFooter */

.preFooter {
    display: flex;
    gap: 2.5rem;
    align-items: center;
    transition: all 0.3s ease;
}

@media (max-width: 768px) {
    .preFooter {
        flex-direction: column;
        gap: 1rem;
    }
}

.preFooter.visible {
    opacity: 1;
    transform: translateY(0rem);
}

.preFooter img {
    width: 60%;
}

@media (max-width: 768px) {
    .preFooter img {
        height: 20rem;
        width: 100%;
        object-fit: cover;
    }
}

.question {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 37rem;
    transition: all 0.3s ease;
}

@media (max-width: 768px) {
    .question {
        padding: 0rem 1rem;
        width: 100%;
        gap: 0rem;
    }
}

.question.visible {
    opacity: 1;
    transform: translateY(0rem);
}

.question h2 {
    font-family: var(--font-family);
    font-size: 2rem;
    font-weight: 600;
    color: #4F4F4F;
}

.question p {
    font-family: var(--font-family);
    font-size: 0.8rem;
    font-weight: var(--font-weight-regular);
    width: 80%;
    color: #292929;
}

@media (max-width: 768px) {
    .question h2 {
        transform: translateY(-4rem);
        font-size: 1rem;
        color: #FBF8EF;
    }

    .question p {
        font-size: 0.5;
        width: 100%;
    }
}